/* style={{ 
  backgroundColor: '#ccff66', 
  height: '100vh', 
  padding: '20px' }} */

  .login_main {
    background-color: #CBD0D8;
    height: 100vh;
    padding: 20px;
  
    display: flex;
    flex-direction: column;
    gap: 30px;
  }
  
  .login_title {
    text-align: center;
  }
  
  .login_input {
    border-radius: 10px;
    border: none;
    outline: none;
    padding: 15px;
    text-align: center;
  
    width: 500px;
    margin: 0 auto;
  }
  
  .button {
    background-color: "#6C7A89";
    padding: 15px;
    
    width: 500px;
    margin: 0 auto;
  }